import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLoading: false,
  },
  mutations: {
    setLoading: function (state, payload) {
      state.isLoading = payload;
    },
  },
  actions: {
    asyncSetLoading: function (content, payload) {
      content.commit("setLoading", payload);
    },
  },
  modules: {},
});
