<template>
  <div class="home">
    <div class="Loading" v-if="isLoading">
      <Loading></Loading>
    </div>
    <div class="input">
      <input
        type="text"
        v-model="kuaidiNumber"
        placeholder="请输入快递单号"
        @focus="isHistory = true"
        @blur="isHistory = false"
      />
      <svg class="icon guanbi" aria-hidden="true">
        <use xlink:href="#icon-guanbi"></use>
      </svg>
      <span class="line"></span>
      <svg class="icon sousuo" aria-hidden="true" @click="searchFn()">
        <use xlink:href="#icon-sousuo"></use>
      </svg>
      <transition name="fade">
        <ul class="history" v-if="isHistory && historyKuaidi.length > 0">
          <li
            class="historyItem"
            v-for="(item, index) in historyKuaidi"
            :key="index"
            @click="kuaidiNumber = item"
          >
            {{ item }}
          </li>
        </ul>
      </transition>
    </div>
    <transition name="fade">
      <div class="searchResult" v-if="isShow">
        <h3>搜索结果</h3>
        <div class="resultTitle">
          <span class="time sort">
            时间
            <span
              class="up"
              :class="{ upActive: isUpActive }"
              @click="upFn()"
            ></span>
            <span
              class="down"
              :class="{ downActive: isDownActive }"
              @click="downFn()"
            ></span>
          </span>
          <span class="location">地点和跟踪进度</span>
        </div>
        <ul class="resultContent">
          <li v-for="(item, index) in showList" :key="index">
            <span
              class="time exactTime fontColor"
              :class="[
                { borderRightActive: isUpActive && index === 0 },
                {
                  borderRightActive:
                    isDownActive && index === showList.length - 1,
                },
              ]"
              >{{ item.time }}
            </span>
            <span
              class="location fontColor"
              :class="[
                { borderRightActive: isUpActive && index === 0 },
                {
                  borderRightActive:
                    isDownActive && index === showList.length - 1,
                },
              ]"
              >{{ item.status }}
            </span>
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
import axios from "../assets/js/SelfAxios";
import Loading from "../components/LottieLoading";
import { mapState, mapActions } from "vuex";
export default {
  name: "Home",
  components: {
    Loading,
  },
  computed: {
    ...mapState(["isLoading"]),
    showList: function () {
      if (this.isUpActive) {
        return [...this.data];
      }
      if (this.isDownActive) {
        return [...this.data].reverse();
      }
    },
  },
  data() {
    return {
      kuaidiNumber: null,
      isShow: false,
      data: null,
      isUpActive: true,
      isDownActive: false,
      historyKuaidi: [],
      isHistory: false,
    };
  },
  mounted() {
    this.historyKuaidi = localStorage.kuaidiList
      ? JSON.parse(localStorage.kuaidiList)
      : [];
  },
  methods: {
    ...mapActions({
      setLoading: "asyncSetLoading",
    }),
    async searchFn() {
      let httpUrl = `composite/queryexpress`;
      let query = `?number=${this.kuaidiNumber}`;
      this.setLoading(true);
      let res = await axios.get(httpUrl + query);
      this.setLoading(false);
      if (res.resp.RespMsg === "快递单号错误") {
        alert(res.resp.RespMsg);
      } else {
        this.isShow = true;
        this.data = res.data.list;
        if (this.historyKuaidi.includes(this.kuaidiNumber) === false) {
          this.historyKuaidi.push(this.kuaidiNumber);
          localStorage.kuaidiList = JSON.stringify(this.historyKuaidi);
        }
      }
    },
    upFn() {
      this.isUpActive = true;
      this.isDownActive = false;
    },
    downFn() {
      this.isDownActive = true;
      this.isUpActive = false;
    },
    sortBy(a, b) {
      return a - b;
    },
  },
};
</script>
